import { authOptions } from "@/lib/auth";
import { connectDB } from "@/lib/mongodb";
import { getServerSession } from "next-auth";
import { NextResponse } from "next/server";
import File from "@/lib/models/File";
import FileVersion from "@/lib/models/FileVersion";
import mongoose from "mongoose";


export async function POST(req: Request) {

    console.log('ok before connect db')
    await connectDB();
    console.log('ok before auth check')
    //session check
    const session = await getServerSession(authOptions)
    if (!session?.user?.id) {
        return NextResponse.json({ error: "Unauthorized" }, { status: 401 })
    }
    console.log('ok before extracting userId')

    const userId = session.user.id

    console.log('ok before req check')

    const { fileId, versionNumber, s3Key } = await req.json()
    console.log('ok before req check 2')
    if (!fileId || !versionNumber || !s3Key) {
        console.log('file id is', fileId)
        console.log('type of fileId is ', typeof fileId)
        console.log('s3 key is ', s3Key)
        return NextResponse.json({ error: "Missing fields required" }, { status: 403 })
    }
    console.log('ok after req check 2 ?????')
    console.log("DEBUG upload/complete");
    console.log("session.user.id:", session.user.id);
    console.log("typeof session.user.id:", typeof session.user.id);
    console.log("fileId from body:", fileId);
    console.log("typeof fileId:", typeof fileId);
    //check the owner 
    const ownerObjectId = new mongoose.Types.ObjectId(userId)

    const file = await File.findOne({ _id: fileId, ownerId: ownerObjectId })
    if (!file) {
        return NextResponse.json({ error: "Forbidden" }, { status: 403 });
    }

    //finalize the version by chaning the pending s3 key to real s3 key in metadata
    const updated = await FileVersion.findOneAndUpdate({
        fileId,
        versionNumber,
        s3Key: "PENDING"
    },
        {
            $set: { s3Key }
        }
    )

    if (!updated) {
        return NextResponse.json({ error: "No pending version found" }, { status: 400 })
    }

    return NextResponse.json({ success: true })

}